<template>
<v-app :dark="isDark">
    <v-toolbar>
        <v-toolbar-side-icon></v-toolbar-side-icon>
        <v-toolbar-title>Title</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="toggleDarkMode()">
            <v-icon>{{ isDark ? 'brightness_1' : 'brightness_3' }}</v-icon>
        </v-btn>
    </v-toolbar>
    <v-content>
        <router-view></router-view>
    </v-content>
</v-app>
</template>

<script>
import { mapMutations } from 'vuex'

export default {
    name: 'App',
    data () {
        return {}
    },
    computed: {
        isDark () {
            return this.$store.state.isDark
        }
    },
    methods: {
        ...mapMutations([
            'toggleDarkMode'
        ])
    }
}
</script>
